.h-half {
    position: absolute;
    top: 50%;
    transform: translate(25%,-50%);
}

/*mobile h-half*/
@media only screen and (max-width: 640px) {
    .h-half {
        position: absolute;
        transform: translate(0%,-30%);
    }
}
.pic-size {
